<template>
  <div class="page">
    <div class="content">
      <ul class="articles list">
        <router-link tag="li" :to="'/articles/'+item.id" v-for="(item,xh) in articles" :key="xh">
          <div class="time">{{item.time}}</div>
          <div class="title">{{item.title}}</div>
          <div class="ms jl" v-html="jie(item.nr)"></div>
          <div class="img" v-if="item.tpic"><img :src="item.tpic"></div>
          <div class="img" v-else><img src="@/assets/myimg/logo.png" style="opacity: .2"></div>
        </router-link>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
    name: 'articles',
    data() {
        return {
            articles: []
        }
    },
    created() {
        fetch('https://www.fastmock.site/mock/5f854ec44ed74b246903c6e75806ec14/api/article').then((rs) => {
            return rs.json()
        }).then((data) => {
            data.sort((a, b) => {
                let atime = a.time.replace(/-/g, '')
                let btime = b.time.replace(/-/g, '')
                if (atime > btime) {
                    return -1
                } else {
                    return 1
                }
            })
            this.articles = data
        })


    },
    methods: {
        jie(html) {
            let much
            let reg = new RegExp('<[^>]+>', 'gi');  //过滤所有的html标签，不包括内容
            if (typeof html != 'string') {  //不是字符串
                return html;
            }
            let last = html.replace(reg, '')
            if (window.innerWidth > 768) {
                much = 60
            } else {
                much = 20
            }

            let lh = last.substr(0, much)
            return '<p>' + lh + '</p>'
        }
    },
    computed: {}
}
</script>

